
import { defineComponent } from "vue";

interface Data {
  animationDuration: number;
  isUp: boolean;
}

export default defineComponent({
  name: "Lift",
  data(): Data {
    return {
      animationDuration: 1,
      isUp: false
    }
  },
  props: {
    boothHeight: {
      type: Number,
      required: true
    },
    data: {
      type: Object,
      required: true,
      position: {
        type: Number,
        required: true
      },
      status: {
        type: String,
        required: true
      },
      id: {
        type: Number,
        required: true
      }
    },
  },
  watch: {
    "data.position": function (newVal, oldVal) {
      this.animationDuration = Math.abs(oldVal - newVal) + 1;
      this.isUp = newVal > oldVal;
      this.$emit("setStatus", { ...this.data, status: "moving" });
      setTimeout(() => {
        this.$emit("setStatus", { ...this.data, status: "waiting" });
        setTimeout(() => {
          this.$emit("setStatus", { ...this.data, status: "ready" });
        }, 3000);
      }, this.animationDuration * 1000);
    }
  }
});
